import { useEffect, useRef, useMemo, useState } from 'react'
import { useFBX, useGLTF, Clone } from '@react-three/drei'
import * as THREE from 'three'
import { useFrame } from '@react-three/fiber'

const hide = ["Balustrade_05_Top"
  , "Building_05_Frames_Top"
  , "Building_05_Glass_Top"
             , "Building_05_Rooftop"
             , "Building_05_Top"]


export default function Buildings(props) {
  const groupRef = useRef()


  const scene = useGLTF("model.glb")
  console.log(scene)
  const [visible, setVisible] = useState(true)
  const toRender = scene ? scene.scene.children : []
  console.log(scene)

  return (     <group ref={groupRef} {...props} dispose={null}>
    {toRender.map(obj =>

        <primitive key={obj.uuid}
                   object={obj}
                   visible={(!hide.includes(obj.children[0].name) )}/>
    )}

  </group>)



}


useGLTF.preload('model.glb')
